  margin-left: 34.04255%;
  margin-right: -100%;
  clear: none;
}

.gallery-columns-3 .gallery-item:nth-child(3n + 3) {
  margin-left: 68.08511%;
  margin-right: -100%;
  clear: none;
}

.gallery-columns-4 .gallery-item {
