/* Main Layout
----------------------------------------------------------------------------------------------------*/
.master-container {
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}

.master-container:after {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  content: "";
  background: linear-gradient(to right, black 0%, transparent 100%);
  opacity: 0.75;
}

.background-cover {
  position: fixed;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  transition: transform 1.2s ease-in-out;
}

.background-cover.loaded {
  transform: scale(1.05);
}

.container {
  position: relative;
  z-index: 2;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
}

.container:after {
  content: " ";
  display: block;
  clear: both;
}

@media screen and (min-width: 64em) {
  .container {
    padding: 0;
  }
  .container.posts-list {
    max-width: 880px;
    margin-left: auto;
    margin-right: auto;
  }
  .container.posts-list:after {
    content: " ";
    display: block;
    clear: both;
  }
}

@media screen and (min-width: 64em) {
  .content {
    width: 74.46809%;
    float: left;
    margin-right: 2.12766%;
  }
}

@media screen and (min-width: 64em) {
  .sidebar {
    width: 23.40426%;
    float: right;
    margin-right: 0;
  }
}

@media screen and (min-width: 64em) {
  .header {
    position: absolute;
    top: 0;
    left: 30px;
    height: 100vh;
    padding-top: 15px;
  }
}

.header__logo {
  display: block;
}

@media screen and (min-width: 64em) {
  .header__logo {
    position: relative;
    left: -25px;
    width: 380px;
    height: auto;
    margin-bottom: 12vh;
  }
}

.main-nav button {
  position: relative;
  display: inline-block;
  padding: 0;
  text-align: left;
  border: 0;
  border-radius: 0;
  outline: none;
  background: none;
  box-shadow: none;
  color: #fff;
  text-transform: uppercase;
  font-family: "Open Sans Condensed", Times, Georgia, serif;
}

.main-nav button span {
  display: block;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 900;
}

.main-nav button:after {
  content: "";
  position: absolute;
  z-index: -1;
  left: -20px;
  top: 50%;
  width: 0;
  background: #5400ff;
  transition: width 0.2s ease-in-out;
}

.main-nav button.active:after {
  width: calc(100% + 20px);
}

.main-nav button:hover:after, .main-nav button:active:after, .main-nav button:focus:after {
  width: calc(100% + 20px);
}

@media screen and (min-width: 64em) {
  .main-nav {
    padding-left: 20px;
  }
  .main-nav li {
    margin-bottom: 30px;
  }
  .main-nav li:first-child {
    margin-bottom: 80px;
  }
  .main-nav li:first-child button {
    font-size: 21px;
  }
  .main-nav li:first-child button span {
    line-height: 47px;
    font-size: 63px;
  }
  .main-nav li:first-child button:after {
    height: 45px;
    margin-top: -20px;
  }
  .main-nav button {
    line-height: 25px;
    font-size: 15px;
  }
  .main-nav button span {
    font-size: 30px;
  }
  .main-nav button:after {
    height: 28px;
    margin-top: -15px;
  }
}

.entry {
  color: #111;
  background: #fff;
}

@media screen and (min-width: 64em) {
  .entry {
    position: absolute;
    top: 0;
    left: 450px;
    width: 574px;
    height: 0;
    padding: 30px;
    opacity: 0;
    overflow: scroll;
    overflow-x: hidden;
  }
}

.entry::-webkit-scrollbar {
  width: 0px;
  /* remove scrollbar space */
  background: transparent;
  /* optional: just make scrollbar invisible */
}

.entry__inner p {
  margin-top: -10px;
  opacity: 0;
}

.entry a {
  position: relative;
  z-index: 1;
  display: inline-block;
  color: #fff;
}

.entry a:after {
  content: "";
  position: absolute;
  z-index: -1;
  left: 4px;
  bottom: 3px;
  width: 0;
  height: 6px;
  background: #5400ff;
  transition: width 0.2s cubic-bezier(0.9, 0, 0, 1);
}

.entry a:hover:after, .entry a:active:after, .entry a:focus:after {
  width: 100%;
}

.entry img {
  display: block;
  max-width: 100%;
  height: auto;
}

.entry__thumbnail img {
  margin: 0 auto 30px;
}

.entry__note__wrapper {
  position: relative;
  top: 25px;
  z-index: 1;
  display: block;
  width: 130px;
  margin: auto;
  line-height: 36px;
  text-align: center;
  font-size: 50px;
  font-weight: 900;
  color: #111;
}

@media screen and (min-width: 64em) {
  .entry__note__wrapper {
    top: 50px;
  }
}

.entry__note__wrapper:after {
  content: "";
  position: absolute;
  z-index: -1;
  top: -10px;
  right: -5px;
  width: 83px;
  height: 33px;
  background: #5400ff;
}

.entry__note {
  color: #5400ff;
}

@media screen and (min-width: 64em) {
  .entry.opening {
    animation: opening 1s cubic-bezier(0.9, 0, 0, 1) forwards;
  }
}

@media screen and (min-width: 64em) {
  .entry.closing {
    animation: closing .5s ease-in-out forwards;
  }
}

@media screen and (min-width: 64em) {
  .entry.opened .entry__inner p {
    animation-name: revealed;
    animation-duration: .3s;
    animation-fill-mode: forwards;
  }
  .entry.opened .entry__inner p:nth-child(1) {
    animation-delay: calc(1 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(2) {
    animation-delay: calc(2 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(3) {
    animation-delay: calc(3 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(4) {
    animation-delay: calc(4 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(5) {
    animation-delay: calc(5 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(6) {
    animation-delay: calc(6 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(7) {
    animation-delay: calc(7 * 0.08s + 0.5s);
  }
  .entry.opened .entry__inner p:nth-child(8) {
    animation-delay: calc(8 * 0.08s + 0.5s);
  }
}

@keyframes opening {
  from {
    height: 0;
    opacity: 0;
  }
  to {
    height: 100vh;
    opacity: 1;
  }
}

@keyframes closing {
  from {
    height: 100vh;
    opacity: 1;
  }
  to {
    height: 0;
    opacity: 0;
  }
}

@keyframes revealed {
  from {
    margin-top: -20px;
    opacity: 0;
  }
